import { h, FunctionalComponent } from '@stencil/core';

import { Name } from '@peculiar/asn1-x509';
import {
  TypeRelationship,
  ActivityDescription,
  WebGDPR,
  InsuranceValue,
  ValuationRanking,
} from '@peculiar/asn1-ntqwac';
import {
  ChallengePassword,
  UnstructuredName,
} from '@peculiar/asn1-pkcs9';

import type { Attribute, TAttributeValue } from '../../../crypto/attribute';
import { RowTitle } from '../row';
import { NameAttribute } from './name_attribute';
import { ActivityDescriptionAttribute } from './activity_description_attribute';
import { WebGdprAttribute } from './web_gdpr_attribute';
import { InsuranceValueAttribute } from './insurance_value_attribute';
import { TypeRelationshipAttribute } from './type_relationship_attribute';
import { ValuationRankingAttribute } from './valuation_ranking_attribute';
import { BasicAttribute } from './basic_attribute';
import { AsStringAttribute } from './as_string_attribute';
import { UnstructuredNameAttribute } from './unstructured_name_attribute';
import { ChallengePasswordAttribute } from './challenge_password_attribute';

const attributeComponentMap: Record<string, FunctionalComponent<{
  attribute: Attribute<TAttributeValue>;
}>> = {
  [Name.name]: NameAttribute,
  [ActivityDescription.name]: ActivityDescriptionAttribute,
  [WebGDPR.name]: WebGdprAttribute,
  [InsuranceValue.name]: InsuranceValueAttribute,
  [TypeRelationship.name]: TypeRelationshipAttribute,
  [ValuationRanking.name]: ValuationRankingAttribute,
  [UnstructuredName.name]: UnstructuredNameAttribute,
  [ChallengePassword.name]: ChallengePasswordAttribute,
};

interface IAttributesProps extends
  IGeneralNameOptions,
  ILeiOptions,
  IAuthorityKeyIdentifierOptions,
  ISubjectKeyIdentifierOptions {
  attributes: Attribute<TAttributeValue>[];
}

export const Attributes: FunctionalComponent<IAttributesProps> = (props) => {
  const { attributes } = props;

  if (!attributes || !attributes.length) {
    return null;
  }

  return ([
    <RowTitle
      value="Attributes"
    />,
    attributes.map((attribute) => {
      try {
        const Component = attributeComponentMap[attribute.value.constructor.name];

        if (Component) {
          return (
            <Component
              attribute={attribute}
            />
          );
        }

        if (typeof attribute.value === 'string') {
          return (
            <AsStringAttribute
              attribute={attribute as Attribute<string>}
            />
          );
        }

        return (
          <BasicAttribute
            attribute={attribute}
          />
        );
      } catch (error) {
        console.error('Error render attribute:', attribute.asn.type);

        return null;
      }
    }),
  ]);
};
